@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400&family=Poppins:ital,wght@0,400;0,700;1,400;1,700&display=swap');

* {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  padding: 0;
  margin: 0;
  background: #24202e;
}

.body {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  align-items: center;
  background: #24202e;
  color: white;
}

header {
  display: flex;
  width: 100%;
  padding: 40px;
  align-items: center;
  font-size: 1em;
  font-style: italic;
}

header img {
  width: 25px;
  height: auto;
  margin-right: 2px;
}

a {
  color: white;
}

.content {
  padding: 40px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  flex: 1;
  align-items: center;
  flex-wrap: wrap;
  gap: 10%;
  max-width: 1280px
}

.bubble {
  border-radius: 100%;
  background: #393642;
  max-width: 200px;
  max-height: 200px;
  width: 20vw;
  height: 20vw;
  min-width: 150px;
  min-height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s;
  font-size: 1.2em;
}

.bubble:hover {
  background: #6ac178;
  cursor: pointer;
}

.content a {
  text-decoration: none;
}

.tableContent {
  padding: 0px 40px;
  display: flex;
  flex-direction: column;
  align-self: flex-start;
}

.tableContent h2 {
  font-size: 3em;
  color: #393642;
  font-weight: bold;
  font-style: italic;
  margin: 10px 0;
}

table th {
  text-align: left;
  min-width: 200px;
  padding: 10px 20px;
  background: #1e1a26;
  border-radius: 5px;
}

table td {
  padding: 10px 20px;
}

table tr:hover > td {
  background: #393642;
  cursor: pointer;
  border-radius: 5px;
}